








import { Component, Vue, Prop } from "vue-property-decorator";
@Component({
    components: {}
})
export default class navBar extends Vue {
    @Prop({ type: String, default: "在线商城" }) private msg?: string;
    @Prop({ type: String, default: "" }) private rightText?: string;
     onClickLeft() {
        window.history.back();
    }
}
